/**
 * @fileoverview This file defines the Leave Approvals page for the HR portal.
 * It provides HR personnel with a view to manage and approve/reject employee leave requests.
 */
import {
    Card,
    CardContent,
    CardHeader,
    CardTitle,
    CardDescription,
} from "@/components/ui/card";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import { Check, Search, X, Eye } from "lucide-react";
import { getLeaveRequests } from "@/lib/data";

/**
 * Renders the HR leave approvals management page.
 * @returns {JSX.Element} The leave approvals page component.
 */
export default async function LeaveApprovalsPage() {
    const leaveRequests = await getLeaveRequests();

    // In a real application, this function would be called when HR approves or rejects a request.
    // const handleUpdateRequest = async (requestId, newStatus) => {
    //   ...
    // }

    return (
        <div className="space-y-6">
            <h1 className="text-3xl font-bold font-headline">Leave Request Approvals</h1>

            {/* Filtering Controls */}
            <Card>
                <CardHeader>
                    <CardTitle>Filter Requests</CardTitle>
                    <CardDescription>
                        Search for requests by employee, type, or status.
                    </CardDescription>
                </CardHeader>
                <CardContent>
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-5 gap-4">
                        <Input placeholder="Search by Employee Name or ID..." />
                        <Select>
                            <SelectTrigger><SelectValue placeholder="Leave Type" /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="all">All Types</SelectItem>
                                <SelectItem value="vacation">Vacation</SelectItem>
                                <SelectItem value="sick">Sick Leave</SelectItem>
                                <SelectItem value="personal">Personal</SelectItem>
                            </SelectContent>
                        </Select>
                         <Select defaultValue="pending">
                            <SelectTrigger><SelectValue placeholder="Status" /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="all">All Statuses</SelectItem>
                                <SelectItem value="pending">Pending</SelectItem>
                                <SelectItem value="approved">Approved</SelectItem>
                                <SelectItem value="rejected">Rejected</SelectItem>
                            </SelectContent>
                        </Select>
                        <Input type="date" />
                        <Button className="w-full">
                            <Search />
                            Search
                        </Button>
                    </div>
                </CardContent>
            </Card>

            {/* Leave Requests Table */}
            <Card>
                <CardHeader>
                    <CardTitle>Incoming Requests</CardTitle>
                    <CardDescription>Review and act on new and existing leave requests.</CardDescription>
                </CardHeader>
                <CardContent>
                    {leaveRequests.length > 0 ? (
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Employee</TableHead>
                                    <TableHead>Type</TableHead>
                                    <TableHead>Dates</TableHead>
                                    <TableHead>Reason</TableHead>
                                    <TableHead>Status</TableHead>
                                    <TableHead className="text-right">Actions</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {leaveRequests.map((req) => (
                                    <TableRow key={req.id}>
                                        <TableCell>
                                            <div className="font-medium">{req.employeeName}</div>
                                            <div className="text-xs text-muted-foreground font-mono">{req.employeeId}</div>
                                        </TableCell>
                                        <TableCell>{req.leave_type}</TableCell>
                                        <TableCell>{req.startDate} - {req.endDate}</TableCell>
                                        <TableCell className="max-w-[200px] truncate">{req.reason}</TableCell>
                                        <TableCell>
                                            <Badge variant={
                                                req.status === 'Approved' ? 'default' 
                                                : req.status === 'Rejected' ? 'destructive' 
                                                : 'secondary'
                                            } className={req.status === 'Approved' ? 'bg-green-500' : ''}>
                                                {req.status}
                                            </Badge>
                                        </TableCell>
                                        <TableCell className="text-right">
                                            {req.status === 'Pending' ? (
                                                <div className="flex gap-2 justify-end">
                                                     <Button variant="outline" size="sm">
                                                        <Eye className="mr-2 h-4 w-4" /> View
                                                    </Button>
                                                    <Button variant="outline" size="sm" className="text-green-600 border-green-600 hover:bg-green-100 hover:text-green-700">
                                                        <Check className="h-4 w-4" /> Approve
                                                    </Button>
                                                    <Button variant="outline" size="sm" className="text-red-600 border-red-600 hover:bg-red-100 hover:text-red-700">
                                                        <X className="h-4 w-4" /> Reject
                                                    </Button>
                                                </div>
                                            ) : (
                                                 <Button variant="outline" size="sm">
                                                    <Eye className="mr-2 h-4 w-4" /> View Details
                                                </Button>
                                            )}
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    ) : (
                        <p className="text-muted-foreground text-center">No leave requests found matching your criteria.</p>
                    )}
                </CardContent>
            </Card>
        </div>
    );
}
