/**
 * @fileoverview This file contains all the functions for fetching data from the database.
 */
'use server';

import { query } from './db';
import { format, formatDistanceToNow, addDays, subDays } from 'date-fns';
import { Users, CheckCircle, Briefcase, Bell } from 'lucide-react';

async function executeQuery(queryString, params, errorMessage) {
    try {
        const { rows } = await query(queryString, params);
        return rows;
    } catch (error) {
        console.error(errorMessage, error);
        throw new Error(errorMessage);
    }
}


export async function getHRDashboardData() {
  const employeeCountQuery = "SELECT COUNT(*) FROM accounts WHERE role = 'Employee' AND status = 'Active'";
  const pendingLeavesQuery = "SELECT COUNT(*) FROM leave_requests WHERE status = 'Pending'";
  const recentActivitiesQuery = `
    SELECT lr.id, a.first_name, a.last_name, lr.leave_type, lr.start_date
    FROM leave_requests lr
    JOIN accounts a ON lr.account_id = a.id
    ORDER BY lr.start_date DESC
    LIMIT 5
  `;

  try {
    const [employeeCountResult, pendingLeavesResult, recentActivitiesResult] = await Promise.all([
      query(employeeCountQuery),
      query(pendingLeavesQuery),
      query(recentActivitiesQuery),
    ]);

    const employeeCount = employeeCountResult.rows[0].count;
    const pendingLeaves = pendingLeavesResult.rows[0].count;
    const recentActivities = recentActivitiesResult.rows.map(row => ({
      id: row.id,
      activity: `${row.first_name} ${row.last_name} requested ${row.leave_type.toLowerCase()} leave`,
      timestamp: formatDistanceToNow(new Date(row.start_date), { addSuffix: true }),
      type: 'Leave'
    }));
    
    return {
      stats: [
        { title: 'Total Employees', value: employeeCount, icon: <Users />, change: '+2 this month' },
        { title: 'On Time Percentage', value: '96%', icon: <CheckCircle />, change: '-1% from last month' },
        { title: 'Active Job Postings', value: '4', icon: <Briefcase />, change: '+1 new posting' },
        { title: 'Pending Leave Requests', value: pendingLeaves, icon: <Bell />, change: `${pendingLeaves} new today` },
      ],
      recentActivities
    };

  } catch (error) {
    console.error('Database Error fetching HR dashboard data:', error);
    throw new Error('Failed to fetch HR dashboard data.');
  }
}

export async function getPastAnnouncements() {
    const queryString = `
        SELECT a.id, a.title, a.content, a.posted_at, a.status, acc.first_name, acc.last_name
        FROM announcements a
        JOIN accounts acc ON a.posted_by_id = acc.id
        ORDER BY a.posted_at DESC
    `;
    const rows = await executeQuery(queryString, [], 'Failed to fetch past announcements.');
    return rows.map(a => ({
        ...a,
        date: format(new Date(a.posted_at), 'yyyy-MM-dd'),
        postedBy: `${a.first_name} ${a.last_name}`
    }));
}


export async function getDailyAttendanceData(date) {
    const requestedDate = format(new Date(date), 'yyyy-MM-dd');
    const queryString = `
        SELECT 
            acc.id, 
            acc.first_name, 
            acc.last_name, 
            acc.position, 
            acc.branch, 
            att.time_in, 
            att.time_out, 
            att.status
        FROM accounts acc
        LEFT JOIN attendance att ON acc.id = att.account_id AND att.attendance_date = $1
        WHERE acc.role = 'Employee' AND acc.status = 'Active'
    `;

    const rows = await executeQuery(queryString, [requestedDate], 'Failed to fetch daily attendance data.');
    return rows.map(emp => ({
        id: emp.id,
        name: `${emp.first_name} ${emp.last_name}`,
        position: emp.position,
        branch: emp.branch,
        timeIn: emp.time_in ? format(new Date(`1970-01-01T${emp.time_in}`), 'HH:mm') : '--',
        timeOut: emp.time_out ? format(new Date(`1970-01-01T${emp.time_out}`), 'HH:mm') : '--',
        status: emp.status || 'Absent'
    }));
}


export async function getEmployees() {
    const queryString = `
        SELECT id, first_name, last_name, position, branch, status 
        FROM accounts 
        WHERE role = 'Employee'`;
    const rows = await executeQuery(queryString, [], 'Failed to fetch employees.');
    return rows.map(acc => ({
        id: acc.id,
        name: `${acc.first_name} ${acc.last_name}`,
        position: acc.position,
        branch: acc.branch,
        status: acc.status
    }));
}

export async function getLeaveRequests() {
    const queryString = `
        SELECT lr.id, lr.leave_type, lr.start_date, lr.end_date, lr.reason, lr.status, a.first_name, a.last_name, a.id as employeeId
        FROM leave_requests lr
        JOIN accounts a ON lr.account_id = a.id
        ORDER BY lr.start_date DESC
    `;
    const rows = await executeQuery(queryString, [], 'Failed to fetch leave requests.');
    return rows.map(lr => ({
        id: lr.id,
        employeeName: `${lr.first_name} ${lr.last_name}`,
        employeeId: lr.employeeid,
        leave_type: lr.leave_type,
        startDate: format(new Date(lr.start_date), 'yyyy-MM-dd'),
        endDate: format(new Date(lr.end_date), 'yyyy-MM-dd'),
        reason: lr.reason,
        status: lr.status
    }));
}

export async function getEmployeesWithPayslipStatus() {
    const queryString = `
        SELECT 
            acc.id, 
            acc.first_name, 
            acc.last_name, 
            acc.position, 
            acc.department,
            EXISTS(SELECT 1 FROM payslips p WHERE p.account_id = acc.id) as has_payslip
        FROM accounts acc
        WHERE acc.role = 'Employee' AND acc.status = 'Active'
    `;
    const rows = await executeQuery(queryString, [], 'Failed to fetch employees with payslip status.');
    return rows.map(emp => ({
        id: emp.id,
        name: `${emp.first_name} ${emp.last_name}`,
        position: emp.position,
        department: emp.department,
        payslipStatus: emp.has_payslip ? 'Generated' : 'Pending'
    }));
}

export async function getEmployeesForScheduling() {
    const queryString = `
        SELECT id, first_name, last_name 
        FROM accounts 
        WHERE status = 'Active' AND role = 'Employee'
    `;
    const rows = await executeQuery(queryString, [], 'Failed to fetch employees for scheduling.');
    return rows.map(emp => ({
        id: emp.id,
        name: `${emp.first_name} ${emp.last_name}`,
        shift: '9:00 - 17:00' // Default shift, can be customized
    }));
}

export async function getEmployeeDashboardData(employeeId) {
    try {
        const employeeQuery = "SELECT id, first_name, last_name, position, department, email FROM accounts WHERE id = $1";
        const announcementsQuery = `
            SELECT id, title, content, posted_at
            FROM announcements
            WHERE status = 'Published'
            ORDER BY posted_at DESC
            LIMIT 5
        `;
        
        const employeeResult = await query(employeeQuery, [employeeId]);
        if (employeeResult.rows.length === 0) {
            throw new Error(`Employee with ID ${employeeId} not found`);
        }
        
        const announcementsResult = await query(announcementsQuery);
        
        const employeeData = employeeResult.rows[0];
        const announcements = announcementsResult.rows.map(a => ({
            id: a.id,
            title: a.title,
            content: a.content,
            date: format(new Date(a.posted_at), 'MMMM d, yyyy')
        }));

        return {
            employee: {
                employeeId: employeeData.id,
                name: `${employeeData.first_name} ${employeeData.last_name}`,
                position: employeeData.position,
                department: employeeData.department,
                email: employeeData.email,
            },
            announcements
        };
    } catch (error) {
        console.error(`Database Error fetching dashboard data for ${employeeId}:`, error);
        throw new Error(`Failed to fetch dashboard data for employee ${employeeId}.`);
    }
}

export async function getAttendanceData(employeeId) {
    const recordsQuery = "SELECT * FROM attendance WHERE account_id = $1 ORDER BY attendance_date DESC";
    const accountQuery = "SELECT available_leaves FROM accounts WHERE id = $1";

    const [recordsResult, accountResult] = await Promise.all([
        query(recordsQuery, [employeeId]),
        query(accountQuery, [employeeId]),
    ]);

    const records = recordsResult.rows;
    const account = accountResult.rows[0];

    const summary = {
        daysAttended: records.filter(r => r.status === 'Present' || r.status === 'Late').length,
        lates: records.filter(r => r.status === 'Late').length,
        absences: records.filter(r => r.status === 'Absent').length,
        totalHours: records.filter(r => r.status === 'Present' || r.status === 'Late').reduce((sum, r) => {
            if(r.time_in && r.time_out) {
                const timeIn = new Date(`1970-01-01T${r.time_in}`);
                const timeOut = new Date(`1970-01-01T${r.time_out}`);
                return sum + (timeOut - timeIn) / (1000 * 60 * 60);
            }
            return sum;
        }, 0).toFixed(2),
        availableLeaves: account?.available_leaves || 0,
    };

    return {
        summary,
        records: records.map(r => ({
            date: format(new Date(r.attendance_date), 'MMMM d, yyyy'),
            timeIn: r.time_in ? format(new Date(`1970-01-01T${r.time_in}`), 'HH:mm') : '--',
            timeOut: r.time_out ? format(new Date(`1970-01-01T${r.time_out}`), 'HH:mm') : '--',
            status: r.status
        }))
    };
}

export async function getPastLeaveRequests(employeeId) {
    const queryString = `
        SELECT id, leave_type, start_date, end_date, status 
        FROM leave_requests 
        WHERE account_id = $1 
        ORDER BY start_date DESC
    `;
    const rows = await executeQuery(queryString, [employeeId], 'Failed to fetch past leave requests.');
    return rows.map(lr => ({
        id: lr.id,
        leave_type: lr.leave_type,
        startDate: format(new Date(lr.start_date), 'yyyy-MM-dd'),
        endDate: format(new Date(lr.end_date), 'yyyy-MM-dd'),
        status: lr.status
    }));
}

export async function createLeaveRequest(leaveRequest) {
    const { employeeId, leaveType, startDate, endDate, reason } = leaveRequest;
    const queryString = `
        INSERT INTO leave_requests (account_id, leave_type, start_date, end_date, reason, status)
        VALUES ($1, $2, $3, $4, $5, 'Pending')
    `;
    await executeQuery(queryString, [employeeId, leaveType, startDate, endDate, reason], 'Failed to create leave request.');
    return { success: true };
}

export async function getPayPeriods(employeeId) {
    const queryString = "SELECT id, period_start, period_end, pay_date, earnings, deductions, net_pay FROM payslips WHERE account_id = $1 ORDER BY pay_date DESC";
    const rows = await executeQuery(queryString, [employeeId], 'Failed to fetch pay periods.');
    return rows.map(p => ({
        id: p.id,
        period: `${format(new Date(p.period_start), 'yyyy-MM-dd')} - ${format(new Date(p.period_end), 'yyyy-MM-dd')}`,
        payDate: format(new Date(p.pay_date), 'MMMM d, yyyy'),
        earnings: p.earnings,
        deductions: p.deductions,
        net_pay: p.net_pay
    }));
}

export async function getSchedule(employeeId) {
    const today = new Date();
    const weekStartDate = subDays(today, (today.getDay() + 6) % 7);
    const queryString = "SELECT day_of_week, time_in, time_out, break_duration_minutes, is_off_day FROM schedules WHERE account_id = $1 AND week_start_date = $2";
    
    const rows = await executeQuery(queryString, [employeeId, format(weekStartDate, 'yyyy-MM-dd')], 'Failed to fetch schedule.');

    return ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'].map((day, i) => {
        const date = addDays(weekStartDate, i);
        const scheduledDay = rows.find(s => s.day_of_week === day);

        if (scheduledDay && !scheduledDay.is_off_day) {
            const timeIn = new Date(`1970-01-01T${scheduledDay.time_in}`);
            const timeOut = new Date(`1970-01-01T${scheduledDay.time_out}`);
            const hours = (timeOut - timeIn) / (1000 * 60 * 60) - (scheduledDay.break_duration_minutes / 60);
            return {
                day,
                date: format(date, 'MMM dd'),
                timeIn: format(timeIn, 'HH:mm'),
                timeOut: format(timeOut, 'HH:mm'),
                break: `${scheduledDay.break_duration_minutes} mins`,
                hours: Math.round(hours)
            };
        }
        return { day, date: format(date, 'MMM dd'), timeIn: 'OFF', timeOut: 'OFF', break: '-', hours: 0 };
    });
}


export async function getPendingEmployeeApprovals() {
    const queryString = `
        SELECT id, first_name, last_name, position, date_hired
        FROM accounts 
        WHERE status = 'Pending Approval'`;
    const rows = await executeQuery(queryString, [], 'Failed to fetch pending employee approvals.');
    return rows.map(acc => ({
        id: acc.id,
        submittedBy: 'HR Department', // This could be joined from another table if available
        employeeName: `${acc.first_name} ${acc.last_name}`,
        position: acc.position,
        submissionDate: format(new Date(acc.date_hired), 'yyyy-MM-dd')
    }));
}

export async function approveEmployee(employeeId) {
    const queryString = "UPDATE accounts SET status = 'Active' WHERE id = $1";
    await executeQuery(queryString, [employeeId], `Failed to approve employee ${employeeId}.`);
}

export async function rejectEmployee(employeeId) {
    const queryString = "DELETE FROM accounts WHERE id = $1";
    await executeQuery(queryString, [employeeId], `Failed to reject employee ${employeeId}.`);
}

export async function getEmployeeName(employeeId) {
    const queryString = "SELECT first_name, last_name FROM accounts WHERE id = $1";
    const rows = await executeQuery(queryString, [employeeId], `Failed to fetch name for employee ${employeeId}.`);
    if (rows.length > 0) {
        return `${rows[0].first_name} ${rows[0].last_name}`;
    }
    return "Unknown Employee";
}

